html,body{
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: none;
    -moz-osx-font-smoothing: grayscale;
}

*{
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: none;
    -moz-osx-font-smoothing: grayscale;
    font-family: 'MDS', SF Pro, -apple-system, Arial, Helvetica, sans-serif;
    font-weight: 700;
    text-decoration: none;
    color: #000000;
    margin: 0;
}


@font-face {
    font-family: 'MDS';
    font-weight: 700;
    src: url('./assets/mds.woff') format("woff"),
         url('./assets/mds.woff2') format("woff2"),
         url('./assets/mds.otf') format("otf")
}